@media (max-width: 768px) {
    .container {
        padding: 0 4vw;
    }

    .section {
        margin-bottom: 10vw;
    }

    .button {
        padding: 3.8vw;
        border-radius: 5vw;
        height: 8.6vw;
        font-size: 3.5vw;

        &_big {
            font-size: 5.6vw;
            padding: 8.6vw 12vw;
            border-radius: 44px;
        }
    }

    .whatsapp {
        height: 7.1vw;
        font-size: 3vw;
    }

    .title-block {
        padding-top: 10vw;

        h1 {
            margin-bottom: 4vw;
            font-size: 7.1vw;
            max-width: 100%;
            line-height: 1.16;
        }

        p {  
            margin-bottom: 1vw;
            max-width: 100%;
        }
    }

    .text {
        font-size: 4.1vw;
        line-height: 1.36;    
    }

    .feedback-block {
        margin-bottom: 10vw;
        &__link {
            margin-left: 3vw;
            font-size: 3.5vw;
        }
    }

    .banner {
        margin-bottom: 10vw;

        &__img {
            mask-image: url("../img/mask/mask-mob.svg");
            mask-repeat: no-repeat;
            mask-size: 100%;
            mask-position: center;
        }
    }

    .key-word {
        font-size: 3vw;
        padding: 1.8vw 2.6vw;
    }

    .key-word-layout {
        margin-bottom: 4vw;
    }

  

    .feedback {
        &__wrapper {
            padding: 6vw;
            gap: 5vw;
            flex-direction: column;
        }

        .second-title {
            text-align: center;
        }

        .second-title, .form {
            width: 100%;
        }
    }

   

    .second-title {
        font-size: 5.4vw;
        line-height: 1.22;
    }

    .third-title {
        font-size: 5.38vw;
    }

  
   
    .popup {
        padding: 6.2vw;
        max-width: 92vw;
        border-radius: 30px;
        background: #EFF4F5;
        max-height: fit-content;

        &_success {
            .second-title {
                margin-bottom: 1.8vw;   
            }

            .text {
                margin-bottom: calc(24vw/3.9);
            }
        }  
        
       
        &__header {
            margin-bottom: clamp(3px, 1.9vw, 40px);
            display: flex;
            gap: 20px;
            justify-content: space-between;
            align-items: start;
        }
    
        &__input, &__button {
            margin-bottom: 10px;
            height: 13.8vw;
            border-radius:20px;
            
        }

        &__input {
            padding-left: 5.1vw;
        }
    
        &__note, &__link-close {
            font-size: 3.1vw;
        }

    }  
    
    .video-wrapper {
        display: block;
        height: 60vw;
    }

    .control-button {
        width: 26%;
    }
    
}